import {useEffect, useState} from 'react';
import Helmet from 'react-helmet';
import styled from 'styled-components';
import $ from 'jquery';

import Top from '../components/top';
import Footer from '../components/footer';
import FooterNavi from '../components/footer_navigation';
import Header from '../components/top_header';

const CSS = styled.div`
    .min-height {
        color: black;
        @media (min-width: 992px) {
            min-height: 100vh;
        }
    }
    .container {
        word-break: keep-all;
        @media (max-width: 992px) {
            br {
                display: none;
            }
        }
        .header {
            font-size: 1.8em;
        }
        .nav-pills {
            justify-content: center;
            border-bottom: 1px solid #F5F5F5;
            .nav-item {
                padding: 0 .25rem;
                .nav-link {
                    transition: .25s;
                    &:hover {
                        color: #8CD790;
                    }
                    &.active {
                        color: #FFF;
                        font-weight: bold;
                        background-color: #AACD6E;
                    }
                }
            }
        }
        .tab-content {
            .tab-pane {
                text-align: center;
            }
            img {
                @media (max-width: 992px) {
                    & {
                        width: 100%;
                    }
                }
            }
        }
        .loading {
            width: 50%;
            margin: auto;
        }
    }
`;

export default function App() {
    const item = {
        0: {
            name: "알로에 사포나리아 꼭순수겔",
            img: "<img src='images/item/sunsu_gel/picture_1.jpg' title='꼭순수겔' alt='꼭순수겔'/>",
            load: false,
        },
        1: {
            name: "에너비45 웻타월",
            img: "<img src='images/item/enurb/picture_1.jpg' title='에너비45' alt='에너비45'/>",
            load: false,
        },
        2: {
            name: "시크릿와이 여성청결제",
            img: "<img src='images/item/secret_therapy/picture_1.jpg' title='아로마 시크릿테라피' alt='아로마 시크릿테라피'/> <img src='images/item/secret_therapy/picture_2.jpg' title='아로마 시크릿테라피' alt='아로마 시크릿테라피'/>",
            load: false,
        },
        3: {
            name: "스테메디 손세정제",
            img: "<img src='images/item/stemedi/picture_1.jpg' title='스테메디' alt='스테메디'/>",
            load: false,
        },
        4: {
            name: "아토케어 머쉬맘",
            img: "<img src='images/item/atocare_mushmom/picture_1.jpg' title='아토케어 머쉬맘' alt='아토케어 머쉬맘'/>",
            load: false,
        }
    }
    const [getUrl, setUrl] = useState(0);
    const [getItem] = useState(item);

    useEffect(() => {
        const noneLoop = () => {
            const location = window.location.href.split("?");
            const select = location[1] === undefined ? 0 : location[1];
            setUrl(select - 1);
        }
        noneLoop();
    });

    useEffect(() => {
        const loadComplete = (index) => {
            $(`#pills-${index} .loading`).remove();
            getItem[Object.keys(getItem)[index]].load = true;
        }
        
        const length = Object.keys(getItem).length;
        for (let i = 0; i < length; i ++) {
            $(`#pills-${i}`).append(getItem[Object.keys(getItem)[i]].img).find("img").on('load', () => {loadComplete(i);});
        }
    }, [getItem]);

    const renderItemList = () => {
        const array = [];
        const length = Object.keys(getItem).length;
        for (let i = 0; i < length; i ++) {
            const activeClass = i === parseInt(getUrl) ? "active" : "";
            array.push(
                <li key={i} className="nav-item" role="presentation">
                    <a className={"nav-link " + activeClass} href={"#pills-" + i} data-toggle="pill" role="tab">
                        {getItem[Object.keys(getItem)[i]].name}
                    </a>
                </li>
            )
        }
        const list = array.map((value) => {
            return value;
        })
        return list;
    }

    const renderItemScreen = () => {
        const array = [];
        const length = Object.keys(getItem).length;
        for (let i = 0; i < length; i ++) {
            const activeClass = i === parseInt(getUrl) ? "active" : "";
            array.push(
                <div key={i} id={"pills-" + i} className={"tab-pane fade show " + activeClass} role="tabpanel">
                    <div className="loading">
                        <img className="w-100" src="images/icon/svg/loading.svg" title="로딩" alt="로딩"/>
                    </div>
                </div>
            );
        }
        const list = array.map((value) => {
            return value;
        })
        return list;
    }

    return <CSS>
        <Helmet>
            <title>제품 서비스</title>
            <meta name="description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>

            <meta property="og:url" content="https://yiyah.co.kr/item?1"/>
            <meta property="og:title" content="제품 서비스"/>
            <meta property="og:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta property="og:image" content="/logo512.png"/>

            <meta name="twitter:url" content="https://yiyah.co.kr/item?1"/>
            <meta name="twitter:title" content="제품 서비스"/>
            <meta name="twitter:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta name="twitter:image" content="/logo512.png"/>
        </Helmet>

        <Top/>
        <Header image={"images/banner/jpg/trees_3.jpg"} text={"제품 서비스"} navi={false}/>

        <div className="container min-height mt-4"
        data-aos="fade-up" data-aos-duration="1000">

            <ul className="nav nav-pills mb-3" id="pills-tab" role="tablist">
                {renderItemList()}
            </ul>
            
            <div className="tab-content mb-4" id="pills-tabContent">
                {renderItemScreen()}
            </div>
        </div>
        <FooterNavi/>
        <Footer/>
    </CSS>
}