import {Fragment} from 'react';
import styled from 'styled-components';
import {NavLink} from 'react-router-dom';

const CSS = styled.div`
    margin-bottom: 1rem;
    .top-banner {
        width: 100%;
        height: 200px;
        background-image: ${props => `url(${props.image})`};
        background-size: cover;
        background-position: center;
    }
    .top-box {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 1rem;
        .text {
            margin-top: 4rem;
            font-size: 2rem;
            color: #AACD6E;
        }
        .sub {
            color: #aaa;
            font-size: .8rem;
        }
        hr {
            width: 5rem;
            border-bottom: 2px solid #3C3530;
            margin-bottom: 0;
        }
    }
    .navi-tab {
        width: 80%;
        @media (max-width: 992px) {
            width: 100%;
        }
        margin: auto;
        color: #FFF;
        text-align: center;
        font-size: 1rem;
        border-top: 1px solid #FFF;
        border-bottom: 1px solid #FFF;
        a {
            padding: .5rem;
            border-right: 1px solid #FFF;
            background-color: rgba(0, 0, 0, .2);
            transition: .25s;
            &:first-child {
                border-left: 1px solid #FFF;
            }
            &:hover {
                color: #FFF;
                background-color: #3C3530;
            }
            &.active {
                color: #FFF;
                background-color: #AACD6E;
            }
        }
    }
`;

export default function App(props) {
    return <CSS image={props.image}>
        <div className="top-banner"/>

        <div className="top-box">
            <div className="container">
                <div className="row navi-tab">
                    {
                        props.navi ? <Fragment>
                        <NavLink to="/intro" className="col">이야네이처 소개</NavLink>
                        <NavLink to="/greet" className="col">대표이사 인사말</NavLink>
                        <NavLink to="/organization" className="col">조직도</NavLink>
                        <NavLink to="/history" className="col">연혁</NavLink>
                        </Fragment> : ""
                    }
                    <div className="w-100">
                        <div className="text"
                        data-aos="fade-up" data-aos-duration="1000">
                            {props.text}
                        </div>
                        <div className="sub"
                        data-aos="fade-up" data-aos-duration="1000" data-aos-delay="400">
                            {props.sub}
                        </div>
                    </div>
                    <hr/>
                </div>
            </div>
        </div>
    </CSS>
}