import Helmet from 'react-helmet';
import styled from 'styled-components';
import {Award, ShieldCheck, BarChartLine, Globe} from 'react-bootstrap-icons';

import Top from '../components/top';
import Footer from '../components/footer';
import Header from '../components/top_header';
import FooterNavi from '../components/footer_navigation';

const CSS = styled.div`
    .min-height {
        color: black;
        @media (min-width: 992px) {
            min-height: 100vh;
        }
    }
    .intro-box {
        position: relative;
        .intro-text {
            position: absolute;
            top: 40%;
            left: 20%;
            @media (max-width: 992px) {
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
            }
            text-align: center;
            width: max-content;
            @media (min-width: 992px) {
                letter-spacing: .25rem;
            }
            .main {
                font-size: 5rem;
                @media (max-width: 992px) {
                    font-size: 3rem;
                }
            }
            .sub {
                line-height: 1.5rem;
                font-size: 3rem;
                @media (max-width: 992px) {
                    font-size: 2rem;
                }
            }
            .circle {
                color: #FFF;
                width: max-content;
                font-size: 2rem;
                padding: .25rem 5rem;
                background-color: #AACD6E;
                border-radius: 2rem;
                margin: auto;
                @media (max-width: 992px) {
                    font-size: 1.5rem;
                }
                a {
                    color: inherit;
                }
            }
        }
    }
    .vision-box {
        color: #FFF;
        background-color: #AACD6E;
        .item {
            margin: .5rem;
            background-color: rgba(0, 0, 0, .3);
            border-radius: .75rem;
            align-items: center;
        }
        .header {
            font-size: 1rem;
            margin: 1rem 0;
            br {
                content: ' ';
                display: block;
                margin-bottom: -.25rem;
            }
            span {
                font-size: .8rem;
            }
        }
    }
    .consume-box {
        overflow: hidden;
        font-size: 1.2rem;
        @media (min-width: 992px) {
            margin: 4rem 10rem;
        }
        img {
            border-radius: .25rem;
        }
    }
    .ci-box {
        background-color: #FFF;
        padding: 5rem 0;
        .width-mobile {
            @media (max-width: 992px) {
                width: 100%;
            }
        }
    }
`;

export default function App() {
    return <CSS>
        <Helmet>
            <title>이야네이쳐 소개</title>
            <meta name="description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>

            <meta property="og:url" content="https://yiyah.co.kr/intro"/>
            <meta property="og:title" content="이야네이처 소개"/>
            <meta property="og:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta property="og:image" content="/logo512.png"/>

            <meta name="twitter:url" content="https://yiyah.co.kr/intro"/>
            <meta name="twitter:title" content="이야네이처 소개"/>
            <meta name="twitter:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta name="twitter:image" content="/logo512.png"/>
        </Helmet>

        <Top/>
        <Header image={"images/banner/jpg/road.jpg"} text={"이야네이처 소개"} sub={"정직과 신뢰를 기반으로 사람과 자연의 조화롭고 건강한 삶을 지향해가는 농업회사법인 '㈜이야네이처'입니다."} navi={true}/>

        <div className="min-height mb-4"
        data-aos="fade-up" data-aos-duration="1000" data-aos-delay="1000">
            <div className="intro-box">
                <img className="w-100" src="images/banner/jpg/simple_background.jpg" alt="이미지" title="이미지"/>
                <div className="intro-text">
                    <div className="sub">친환경 브랜드</div>
                    <div className="main">이야네이처</div>
                    <div className="circle">
                        <a href="https://smartstore.naver.com/naemomaenature" target="_blank" rel="noopener noreferrer">
                            바로 가기
                        </a>
                    </div>
                </div>
            </div>
            <div className="vision-box">
                <div className="container p-5">
                    <div className="row text-center">
                        <div className="col-6 mb-4">
                            <div className="row item h-100"
                            data-aos="fade-right">
                                <div className="col-lg-5 col-12 header">
                                    <Award className="mb-2" size="4rem"/>
                                    <div>비전 <br/><span>VISION</span></div>
                                </div>
                                <div className="col-lg-7 col-12 description">
                                    자연과 사람의 조화 그리고, 나눔 <br/>
                                    자연을 존중하는 환경 중심 경영 <br/>
                                    신뢰와 정도의 가치 실헌으로 <br/>
                                    건강을 지키고(보호하고) <br/>
                                    행복을 계승하는 기업
                                </div>
                            </div>
                        </div>
                        <div className="col-6 mb-4">
                            <div className="row item h-100"
                            data-aos="fade-left">
                            <div className="col-lg-5 col-12 header">
                                    <ShieldCheck className="mb-2" size="4rem"/>
                                    <div>믿음 <br/><span>TRUST</span></div>
                                </div>
                                <div className="col-lg-7 col-12 description">
                                    안전하고 다양하며, <br/>
                                    엄선된 원료와 최상의 <br/>
                                    품질관리로써 <br/>
                                    인증된 제품만을 제공
                                </div>
                            </div>
                        </div>
                        <div className="col-6">
                            <div className="row item h-100"
                            data-aos="fade-right" data-aos-placeholder="center-bottom" data-aos-once="false">
                                <div className="col-lg-5 col-12 header">
                                    <BarChartLine className="mb-2" size="4rem"/>
                                    <div>가치 <br/><span>VALUES</span></div>
                                </div>
                                <div className="col-lg-7 col-12 description">
                                    자연에서 얻은 귀한 재료들로 <br/>
                                    고객의 건광과 행복을 지키겠다는 약속
                                </div>
                            </div>
                        </div>
                        <div className="col-6">
                            <div className="row item h-100"
                            data-aos="fade-left" data-aos-placeholder="center-bottom" data-aos-once="false">
                                <div className="col-lg-5 col-12 header">
                                    <Globe className="mb-2" size="4rem"/>
                                    <div>나눔 <br/><span>SHARE</span></div>
                                </div>
                                <div className="col-lg-7 col-12 description">
                                    사람과 사람, <br/>
                                    마음과 마음의 나눔, <br/>
                                    인화의 가치 창조
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            {/* <div className="ci-box">
                <div className="container"
                data-aos="fade-up">
                    <div className="row">
                        <div className="col-lg-6 col-12 text-right">
                            <img className="width-mobile" src="images/main/png/yiyah_brand.png" alt="CI" title="CI"/>
                        </div>
                        <div className="col-lg-6 col-12 d-flex align-items-center justify-content-center text-lg-left text-center mt-lg-0 mt-4">
                            세계로 뻗어나가는 글로벌 기업의 이상과 방향성을 상징한 이미지이며, <br/>
                            문자 부분의 YIYAH는 담백한 전용 서체로 표현되어 <br/> 
                            전체적으로 깨끗한 기업 이미지와 매칭됩니다. <br/>
                            문자 YIYAH의 먹색 부분은 백색 바탕과 결합되어 또 다른 균형의 미를 상징함으로써, <br/> 
                            상반된 것들이 조화를 이루며 그 안에서 <br/>
                            최고의 가치와 균형잡힌 기업 문화의 창달을 목표로 하는 의지를 나타냅니다.
                        </div>
                    </div>
                </div>
            </div> */}
            <div className="consume-box">
                <div className="card-deck text-center p-lg-0 p-5">
                    <div className="card"
                    data-aos="fade-right">
                        <img className="card-img-top" src="images/banner/jpg/cons.jpg" alt="사진" title="사진"/>
                        <div className="card-body">
                            <h5 className="card-title">경영 방침</h5>
                            <p className="card-text">
                                제품 기획 단계에서부터 판매에 이르기까지 <br/>
                                고객의 입장에서 한 번 더 생각하고 실천하여 <br/>
                                고객 감동을 위해 과감히 도전하며 <br/>
                                미래의 꿈도 고객과 함께 거듭나겠습니다.
                            </p>
                        </div>
                    </div>
                    <div className="card"
                    data-aos="fade-left">
                        <img className="card-img-top" src="images/banner/jpg/cons_2.jpg" alt="사진" title="사진"/>
                        <div className="card-body">
                            <h5 className="card-title">경영 방침</h5>
                            <p className="card-text">
                                저희의 끝없는 창조적 도전이 <br/>
                                소비자분들께는 보다 여유롭고 윤택한 생활과 <br/>
                                문화로 이어질 수 있다는 신념을 가지고 <br/>
                                앞으로도 인류의 아름다움과 건강, <br/>
                                편의에 기여하도록 최선을 다하겠습니다.
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <FooterNavi/>
        <Footer/>
    </CSS>
}