import {NavLink} from 'react-router-dom';
import $ from 'jquery';
import styled from 'styled-components';
import {mainCategory, subCategory} from '../values/url';

const CSS = styled.div `
    // PC
    user-select: none;
    z-index: 1000;
    .list-box {
        display: none;
        position: fixed;
        top: 1rem;
        right: 1rem;
        z-index: 1500;
    }
    .background-alpha {
        padding: .5rem;
        border-radius: 2rem;
        background-color: rgba(0, 0, 0, .2);
    }
    @media (min-width: 992px) {
        position: ${props => props.background === false ? 'fixed' : 'absolute'};
        top: 0;
        width: 100%;
        .logo-box { 
            display: ${props => props.background === false ? 'none': 'block'};
            text-align: center;
            margin-bottom: 1rem;
            .logo {
                cursor: pointer;
                width: 220px;
                height: auto;
                transition: ease-in-out 1s;
            }
            .logo:hover {
                transform: rotateX(360deg);
            }
        }
        .category {
            cursor: pointer;
            position: relative;
            transition: .25s;
            &:hover .title {
                color: ${props => props.background === false ? "#AACD6E" : "#AACD6E"};
            }
            &:hover .title::before {
                width: 40%;
                opacity: 1;
            }
            &:hover .menu-box {
                opacity: 1;
                transform: translate(-50%, 0%) scale(1);
            }
            .title {
                color: ${props => props.background === false ? "#FFF" : "#FFF"};
                text-align: center;
                transition: .5s;
                &:before {
                    content: '';
                    position: absolute;
                    top: 25px;
                    left: 0;
                    margin-left: 30%;
                    width: 0%;
                    height: 1px;
                    background-color: #AACD6E;
                    opacity: 0;
                    transition: ease-in-out .5s;
                }
            }
            .menu-box {
                text-align: left;
                position: absolute;
                top: 0;
                left: 50%;
                padding: .25rem 0;
                padding-top: 2.5rem;
                transform: translate(-50%, -50%) scale(0);
                font-size: .9rem;
                width: 180px;
                opacity: 0;
                z-index: 500;
                transition: ease-in-out .35s;
                .menu {
                    background-color: white;
                    border: 1px solid rgba(0, 0, 0, 0.125);
                    border-radius: .35rem;
                    padding: 0 1.5rem;
                    li {
                        color: black;
                        position: relative;
                        padding: 1rem 0;
                        transition: .5s;
                        &:hover {
                            color: #4f953b;
                        }
                        &::after {
                            content: '';
                            position: absolute;
                            bottom: 10px;
                            left: 0;
                            width: 100%;
                            height: 1px;
                            background-color: rgba(0, 0, 0, .1);
                        }
                        &::before {
                            content: '';
                            position: absolute;
                            bottom: 10px;
                            left: 0;
                            width: 0%;
                            height: 1px;
                            transition: .5s;
                        }
                        &:hover::before {
                            width: 100%;
                            background-color: #8CD790;
                        }
                    }
                }
            }
        }
    }
    @media (max-width: 992px) {
        .list-box {
            cursor: pointer;
            display: block;
            padding: 1rem;
            background-color: rgba(0, 0, 0, 0.5);
            border-radius: 1rem;
            .line {
                width: 18px;
                height: 2px;
                background-color: white;
                margin-bottom: 5px;
                &:last-child {
                    margin-bottom: 0;
                }
            }
        }
        .container {
            position: fixed;
            top: 0;
            left: 0;
            max-width: 100vw;
            height: 100%;
            z-index: 1000;
            transform: rotateY(90deg);
            transition: ease .5s;
            &.active, &.active::before {
                transform: rotateY(0deg);
            }
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100vw;
                height: 100%;
                background-image: url("images/banner/jpg/aloe_2.jpg");
                background-size: cover;
                background-repeat: no-repeat;
                filter: brightness(0.4);
                transform: rotateY(90deg);
                transition: ease .5s;
            }
        }        
        .logo-box {
            padding: 0 5rem;
            margin-top: 12rem;
            margin-bottom: 2rem;
            .logo {
                width: 100%;
                height: auto;
                transition: .25s;
                &:hover {
                    transform: scale(0.95);
                }
            }
        }
        .category {
            margin-bottom: 2rem;
            .title {
                color: white;
                text-align: center;
                margin-bottom: .75rem;
                background-color: rgba(0, 0, 0, 0.125);
                border: 1px solid #44633F;
                border-radius: 1rem;
            }
            li {
                color: white;
                text-align: center;
                padding: .25rem 0;
                transition: .5s;
                &:hover {
                    color: #30A9DE;
                }
            }
        }
    }
`;

export default function App(props) {
    const fuctionRenderCategory = () => {
        const arrayData = [];
        const array = Object.keys(subCategory).length;
        for (let i = 0; i < array; i ++) {
            const pushData = [];
            const title = mainCategory[Object.keys(subCategory)[i]].string;
            const data = subCategory[Object.keys(subCategory)[i]];
            const dataLength = Object.keys(data).length;
            for (let k = 0; k < dataLength; k ++) {
                const list = data[Object.keys(data)[k]];
                if (i === 2) { // 주소 넘기기
                    pushData.push(
                        <a key={k} href={list.href} target="_blank" rel="noopener noreferrer"><li>{list.string}</li></a>
                    );
                }
                else { // 리액트 페이지
                    pushData.push(
                            <NavLink key={k} to={list.href}><li>{list.string}</li></NavLink>
                    );
                }
            }
            arrayData.push(
                <div key={i} className="category col-lg-3 col-6">
                    <div className="title">{title}</div>
                    <div className="menu-box">
                        <ul className="menu">
                            {pushData.map((value) => {return value})}
                        </ul>
                    </div>
                </div>
            );
        }
        return arrayData.map((value) => {return value})
    }
    const styleBackground = props.background !== undefined ? props.background : true;
    const stylePagelist = props.pagelist !== undefined ? props.pagelist : true;

    return <CSS background={styleBackground} pagelist={stylePagelist}>
        <div className="list-box" onClick={
            () => {
                const box = $("#container-box");
                (box.hasClass("active")) ? box.removeClass("active") : box.addClass("active");
            }
        }>
            <div className="line"/>
            <div className="line"/>
            <div className="line"/>
        </div>
        <div id="container-box" className="container p-4">
            <div className="row">
                <div className="logo-box col-12">
                    <NavLink to="/"><img className="logo" src="images/logo/svg/logo_6.svg" alt="로고" title="이야네이처"/></NavLink>
                </div>
                <div className="col-12 m-auto">
                    <div className="row background-alpha">
                        {fuctionRenderCategory()}
                    </div>
                </div>
            </div>
        </div>
    </CSS>
}