import {useEffect, useState} from 'react';
import {NavLink} from 'react-router-dom';
import $ from 'jquery';
import styled from 'styled-components';
import {HouseFill, CaretDownFill} from 'react-bootstrap-icons';
import {mainCategory, subCategory} from '../values/url';

const CSS = styled.div `
    #pagelist {
        display: ${props => props.background === false ? "none" : "block"};
        background-color: #AACD6E;
        color: #FFF;
        font-size: 1rem;
        .padding-box {
            position: relative;
            transition: 0.25s;
            .drop-box {
                display: none;
                position: absolute;
                top: 27px;
                left: 0;
                width: 100%;
                text-align: center;
                background-color: #AACD6E;
                padding: .25rem 0;
                z-index: 100;
                &.active {
                    display: block;
                }
                li {
                    color: white;
                    padding: 0.25rem 0;
                    transition: 0.25s;
                    &:hover {
                        color: #534847;
                    }
                }
            }
            .left-right-border {
                padding: 0 1rem;
                border-left: 1px solid rgba(0, 0, 0, 0.125);
                border-right: 1px solid rgba(0, 0, 0, 0.125);
            }
            .right-border {
                padding: 0 2rem;
                border-right: 1px solid rgba(0, 0, 0, 0.125);
            }
            .page-button, .home-button {
                cursor: pointer;
                transition: 0.25s;
                &:hover {
                    color: #534847;
                }
            }
            .page-button {
                transition: 0.25s;
                &.active {
                    color: #534847;
                }
            }
        }
    }
`;

export default function App() {
    const [getMainCategory, setMainCategory] = useState("메인 카테고리");
    const [getSubCategory, setSubCategory] = useState("서브 카테고리");

    const [getlistCategory, setlistCategory] = useState({});

    useEffect(() => {
        const location = window.location.pathname;
        switch(location) { // JSON 탐색후 href 값과 동일한 값을 기준으로 상위 JSON까지 가져오면 해결됨
            default: break;
            case "/intro": setMainCategory(mainCategory.a.string); setSubCategory(subCategory.a.a.string); setlistCategory(subCategory.a); break;
            case "/greet": setMainCategory(mainCategory.a.string); setSubCategory(subCategory.a.b.string); setlistCategory(subCategory.a); break;
            case "/organization": setMainCategory(mainCategory.a.string); setSubCategory(subCategory.a.c.string); setlistCategory(subCategory.a); break;
            case "/history": setMainCategory(mainCategory.a.string); setSubCategory(subCategory.a.d.string); setlistCategory(subCategory.a); break;
            case "/item": setMainCategory(mainCategory.b.string); setSubCategory(subCategory.b.a.string); setlistCategory(subCategory.b); break;
            case "/inquiry": setMainCategory(mainCategory.d.string); setSubCategory(subCategory.d.a.string); setlistCategory(subCategory.d); break;
        }
    }, []);

    const fuctionRenderNav = () => {
        let list = "";
        if (Object.keys(getlistCategory).length > 0) {
            list = Object.values(getlistCategory).map((value, index) => {
                return <NavLink key={index} to={value.href}><li className="drop-item">{value.string}</li></NavLink>
            })
        }
        return list;
    }

    return <CSS>
        <div id="pagelist">
            <div className="container p-1">
                <div className="d-flex p-1">
                    <div className="padding-box">
                        <div className="left-right-border">
                            <NavLink to="/"><HouseFill className="home-button" size="1.2rem"/></NavLink>
                        </div>
                    </div>
                    <div className="padding-box">
                        <div className="right-border">
                            <div className="page-button" onClick={
                                (event) => {
                                    $(event.currentTarget).toggleClass("active");
                                    const drop = $(".drop-box");
                                    if (drop.length > 0) {
                                        $(drop[0]).toggleClass("active");
                                    }
                                }
                            }>
                                {getMainCategory} <CaretDownFill size="1rem"/>
                            </div>
                        </div>
                        <ul id="drop" className="drop-box"> {
                                Object.keys(mainCategory).map((value, index) => {
                                    return <NavLink key={index} to={mainCategory[value].href}>
                                        <li className="drop-item">{mainCategory[value].string}</li>
                                    </NavLink>
                                })
                            }
                        </ul>
                    </div>
                    <div className="padding-box">
                        <div className="right-border">
                            <div className="page-button" onClick={
                                (event) => {
                                    $(event.currentTarget).toggleClass("active");
                                    const drop = $(".drop-box");
                                    if (drop.length > 0) {
                                        $(drop[1]).toggleClass("active");
                                    }
                                }
                            }>
                                {getSubCategory} <CaretDownFill size="1rem"/>
                            </div>
                        </div>
                        <ul id="drop" className="drop-box"> {
                            fuctionRenderNav()
                        }
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </CSS>
}