import Helmet from 'react-helmet';
import styled from 'styled-components';

import Top from '../components/top';
import Footer from '../components/footer';
import Header from '../components/top_header';
import FooterNavi from '../components/footer_navigation';

const CSS = styled.div`
    .min-height {
        color: black;
        @media (min-width: 992px) {
            min-height: 100vh;
        }
    }
    .container {
        @media (max-width: 992px) {
            br {
                display: none;
            }
        }
        .header {
            font-size: 1.8em;
        }
        textarea {
            min-height: 10rem;
        }
        .form-check-input {
            margin-top: none;
        }
        .check-button {
            display: flex;
            align-items: center;
        }
        .btn-success {
            background-color: #AACD6E;
            border-color: #AACD6E;
        }
    }
`;

export default function App() {
    return <CSS>
        <Helmet>
            <title>문의사항 접수</title>
            <meta name="description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>

            <meta property="og:url" content="https://yiyah.co.kr/inquiry"/>
            <meta property="og:title" content="문의사항 접수"/>
            <meta property="og:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta property="og:image" content="/logo512.png"/>

            <meta name="twitter:url" content="https://yiyah.co.kr/inquiry"/>
            <meta name="twitter:title" content="문의사항 접수"/>
            <meta name="twitter:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta name="twitter:image" content="/logo512.png"/>
        </Helmet>

        <Top/>
        <Header image={"images/banner/jpg/environment.jpg"} text={"문의사항 접수"} navi={false}/>
        
        <div className="container min-height mb-5"
        data-aos="fade-up" data-aos-duration="1000">
            <form method="post" target="_blank" action="https://script.google.com/macros/s/AKfycbyHk6Rnj1EZLUQUJZYzri4ch-u0MxCp0l67LLA3MpNF00ovKKY/exec"
            data-aos="fade-down" data-aos-duration="1000">
                <div className="form-row">
                    <div className="col-12 mb-3">
                        <label htmlFor="form-input-name">이름</label>
                        <input name="이름" id="form-input-name" className="form-control" type="text" required/>
                    </div>
                    <div className="col-12 mb-3">
                        <label htmlFor="form-input-email">이메일</label>
                        <input name="이메일" id="form-input-email" className="form-control" type="text" placeholder="XXX@XXX.XXX" required/>
                    </div>
                    <div className="col-12 mb-3">
                        <label htmlFor="form-input-number">연락처</label>
                        <input name="연락처" id="form-input-number" className="form-control" type="number" maxLength="12" placeholder="XXX-XXXX-XXXX" required/>
                    </div>
                    <div className="col-12 mb-3">
                        <label htmlFor="form-input-text">내용</label>
                        <textarea name="내용" id="form-input-text" className="form-control" maxLength="500" required/>
                    </div>
                    <div className="form-group form-check check-button ml-2">
                        <input name="개인정보동의" type="checkbox" className="form-check-input" id="check" required/>
                        <label className="form-check-label ml-lg-0 ml-2" htmlFor="check">현재 기입된 개인정보의 이용에 동의합니다.</label>
                    </div>
                </div>
                <div className="d-flex justify-content-center">
                    <button className="btn btn-success w-100" type="submit">전송</button>
                </div>
            </form>
        </div>
        <FooterNavi/>
        <Footer/>
    </CSS>
}