import {useEffect} from 'react';
import {CaretUpFill, CaretDownFill} from 'react-bootstrap-icons';
import $ from 'jquery';
import styled from 'styled-components';

const CSS = styled.div`
  .button-wrap {
    position: fixed;
    border-radius: 0.5rem;
    background-color: rgba(255, 255, 255, 0.6);
    box-shadow: 0px 1px 1px 1px rgba(0, 0, 0, 0.125);
    opacity: 0;
    transform: scaleY(0);
    transition: 0.25s;
  }
  .button-top {
    width: 2.5rem;
    height: 2.5em;
    right: 5rem;
    bottom: 2rem;
  }
  .button-bottom {
    width: 2.5rem;
    height: 2.5em;
    right: 2rem;
    bottom: 2rem;
  }
  .button-wrap .img {
    position: absolute;
    width: 100%;
    bottom: 30%;
  }
  .button-wrap.active {
    cursor: pointer;
    transform: scaleY(1);
    opacity: 1;
  }
  .button-wrap.active:hover {
    background-color: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 4px 2px rgba(0, 0, 0, 0.125);
    transform: scale(0.95);
  }
`;

export default function App()
{
  const scrollTop = () => {
    const check = $("#scroll-button-top.active");
    if (check !== null)
    {
      const html = $("html, body");
      html.animate({scrollTop: 0}, 600);
    }
  }

  const scrollBottom = () => {
    const check = $("#scroll-button-bottom.active");
    if (check !== null)
    {
      const html = $("html, body");
      html.animate({scrollTop: html.height()}, 1000);
    }
  }

  const viewCheck = () => {
    if($(window).scrollTop() <= 0) {
      $("#scroll-button-top").removeClass("active");
    } else {
      $("#scroll-button-top").addClass("active");
    }

    if($(window).scrollTop() + $(window).height() === $(document).height()) {
      $("#scroll-button-bottom").removeClass("active");
    } else {
      $("#scroll-button-bottom").addClass("active");
    }
  }    

  useEffect(() => {
    $(window).scroll(() => {
      viewCheck();
    });
  }, []);

  useEffect(() => {
    viewCheck();
  });

  return <CSS>
    <div id="scroll-button-top" className="button-wrap button-top" onClick={scrollTop}>
      <CaretUpFill className="img"/>
    </div>
    <div id="scroll-button-bottom" className="button-wrap button-bottom" onClick={scrollBottom}>
      <CaretDownFill className="img"/>
    </div>
  </CSS>;
}