import Helmet from 'react-helmet';
import styled from 'styled-components';

import Top from '../components/top';
import Footer from '../components/footer';
import Header from '../components/top_header';
import FooterNavi from '../components/footer_navigation';

const CSS = styled.div`
    .min-height {
        color: black;
        @media (min-width: 992px) {
            min-height: 100vh;
        }
    }
`;

export default function App() {
    return <CSS>
        <Helmet>
            <title>조직도</title>
            <meta name="description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>

            <meta property="og:url" content="https://yiyah.co.kr/organization"/>
            <meta property="og:title" content="조직도"/>
            <meta property="og:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta property="og:image" content="/logo512.png"/>

            <meta name="twitter:url" content="https://yiyah.co.kr/organization"/>
            <meta name="twitter:title" content="조직도"/>
            <meta name="twitter:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta name="twitter:image" content="/logo512.png"/>
        </Helmet>

        <Top/>
        <Header image={"images/banner/jpg/trees_2.jpg"} text={"조직도"} navi={true}/>

        <div>
            <div className="container mt-4 mb-5"
            data-aos="fade-up" data-aos-duration="1000" data-aos-delay="600">
                <div className="intro-text">
                    <img className="w-100 h-100" src="images/main/svg/organization_color.svg" title="조직도" alt="조직도"/>
                </div>
            </div>
        </div>
        <FooterNavi/>
        <Footer/>
    </CSS>
}