import Helmet from 'react-helmet';
import styled from 'styled-components';

import Top from '../components/top';
import Footer from '../components/footer';
import Header from '../components/top_header';
import FooterNavi from '../components/footer_navigation';

const CSS = styled.div`
    .min-height {
        color: black;
        @media (max-width: 992px) {
            min-height: 100vh;
        }
    }
`;

export default function App() {
    return <CSS>
        <Helmet>
            <title>존재하지 않는 페이지</title>
            <meta name="description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>

            <meta property="og:url" content="https://yiyah.co.kr/empty"/>
            <meta property="og:title" content="존재하지 않는 페이지"/>
            <meta property="og:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta property="og:image" content="/logo512.png"/>

            <meta name="twitter:url" content="https://yiyah.co.kr/empty"/>
            <meta name="twitter:title" content="존재하지 않는 페이지"/>
            <meta name="twitter:description" content='"정성으로 키워 내몸愛 담다." 엘라뮤즈 온라인 공식판매처'/>
            <meta name="twitter:image" content="/logo512.png"/>
        </Helmet>

        <Top/>
        <Header image={"images/banner/jpg/road.jpg"} text={"존재하지 않는 페이지"} sub={"존재하지 않는 페이지입니다."} navi={true}/>

        <div className="min-height text-center">
            잘못된 페이지를 접근하셨습니다.
        </div>
        <FooterNavi/>
        <Footer/>
    </CSS>
}