// import {useEffect} from 'react';
import styled from 'styled-components';
import {Signpost, TelephoneInbound} from 'react-bootstrap-icons';

import Top from '../components/top';
import Footer from '../components/footer';

const CSS = styled.div`
    .main-banner {
        width: 100%;
        height: 300px;
        background-image: url("images/banner/jpg/aloe.jpg");
        background-size: cover;
        @media (max-width: 992px) {
            & {
                height: 200px;
            }
        }
    }
    .min-height {
        color: black;
        @media (min-width: 992px) {
            min-height: 100vh;
        }
    }
    .container {
        word-break: keep-all;
        @media (max-width: 992px) {
            br {
                display: none;
            }
        }
        .header {
            font-size: 1.8em;
        }
        .icon-box {
            color: #56445D;
            background-color: #C5E99B;
            border: 1px solid #56445D;
            border-radius: 1rem;
            padding: 0.25rem;
        }
        .text {
            font-size: 0.9rem;
        }
    }
`;

export default function App() {
    return <CSS>
        <Top/>
        <div className="main-banner"/>

        <div className="container min-height mt-4"
        data-aos="fade-up" data-aos-duration="1000">
            <div className="header mb-2">
                위치 및 길 안내
            </div>
            <iframe title="내몸애자연 길 찾기" src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3170.731610803934!2d126.94183596239696!3d37.37252698884552!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x357b6759b349983f%3A0x14817a0ab8172ea1!2z7JWI7JaR6rWt7KCc7Jyg7Ya164uo7KeA!5e0!3m2!1sko!2skr!4v1606043985842!5m2!1sko!2skr" width="100%" height="450" frameborder="0" allowfullscreen="" aria-hidden="false" tabindex="0"
            data-aos="zoom-in" data-aos-duration="1500"/>
            <div className="text mt-4">
                <p data-aos="fade-up" data-aos-duration="1000" data-aos-once="true">
                    <Signpost className="icon-box mr-2" size="1.5rem"/> 주소 : 경기 안양시 동안구 호계동 555-9 국제유통단지 지원동 <br/>
                </p>
                <p data-aos="fade-up" data-aos-duration="1000" data-aos-once="true">
                    <TelephoneInbound className="icon-box mr-2" size="1.5rem"/> TEL : 1877-5751
                </p>
            </div>
        </div>
        <Footer/>
    </CSS>
}