import {useEffect} from 'react';
import {Route, Switch, withRouter} from 'react-router-dom';
import $ from 'jquery';
import AOS from 'aos';
import NProgress from 'nprogress';

import Scrolling from './components/scrolling';
import HomePage from './pages/home';
import IntroPage from './pages/intro';
import GreetPage from './pages/greet';
import GuidePage from './pages/guide';
import OrganizationPage from './pages/organization';
import HistoryPage from './pages/history';
import ItemPage from './pages/item';
import InquiryPage from './pages/inquiry';
import EmptyPage from './pages/empty';

function App() {
  AOS.init({
    duration: 1000,
    anchorPlacement: 'top-bottom'
  });

  useEffect(() => {
    NProgress.configure({minimum: 0.6, easing: 'ease', speed: 500, trickleSpeed: 1000});
    NProgress.done();
    $('html, body').animate({scrollTop: 0}, 500);
    const box = $("#container-box");
    if (box.hasClass("active")) {box.removeClass("active");}
    return () => {
      NProgress.start();
    }
  });

  return <>
    <Switch>
      <Route exact path="/"><HomePage/></Route>
      <Route path="/intro"><IntroPage/></Route>
      <Route path="/greet"><GreetPage/></Route>
      <Route path="/guide"><GuidePage/></Route>
      <Route path="/organization"><OrganizationPage/></Route>
      <Route path="/history"><HistoryPage/></Route>
      <Route path="/item"><ItemPage/></Route>
      <Route path="/inquiry"><InquiryPage/></Route>
      <Route exact path=""><EmptyPage/></Route>
    </Switch>

    <Scrolling/>
  </>
}

export default withRouter(App);